.btn {
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  align-items: center;
  letter-spacing: 2px;
  display: inline-flex;
  margin: 10px 0;
  padding: 13px 6px 14px 5px;
  text-transform: uppercase;
  border-radius: 4px;

  color: $color-default-white;

  background-color: $color-default-red;
  border: none;
  cursor: pointer;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }
  }
}
